@mixin margin-killer {
  margin: 0;
  padding: 0;
}

@mixin list-killer {
  list-style: none;
  margin: 0;
  padding: 0;
}

@mixin container {
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

@mixin icon {
  content: "";
  position: absolute;
  top: 3px;
  left: calc(50% - 24px);
  width: 47px;
  height: 47px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

@mixin rangeThumb {
  position: relative;
  width: 23px;
  height: 26px;
  background-image: url('../img/cursor.svg');
  cursor: pointer;
  background-color: transparent;
  border: none;
}

@mixin rangeTrack {
  width: 100%;
  height: 2px;
  cursor: pointer;
  background: transparent;
}
